@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap');


*{
    font-family: 'Roboto', sans-serif;
    font-size: 19x;
}

body{
    display: flex;
    justify-content: center;
    align-items: center;
}

.container{
    top:50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    margin: 0 auto;
    width:256px;
    text-align: center;
    border: 10px solid black;
    background: black;
    border-radius: 5px;
}

.keypad{
    display:grid;
    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(60px,auto);
}

input[type="text"]{
    height:75px;
    width: 249px;
    background-color: black;
    border: 0;
    color: white;
    text-align: right;
    font-size: 25px;
    font-weight: 500;
    letter-spacing: 1px;
    border: 2px solid rgb(78, 74, 74);
    border-radius: 5px;
}

#clear{
    grid-column: 1/3;
    grid-row:1;
    font-weight: 700;
}

#result{
    grid-column:3/5;
    grid-row: 5;
}


button{
    margin:5px;
    border-radius: 5px;
    cursor:pointer;
    background:  #262834;
    color: white;
    font-weight: 700;
}


input:focus,
input:active,
button:focus,
button:activ{
    outline: none;;
}

.highlight{
    background-color: rgb(15, 161, 161);
    font-weight: 900;
}